import {Component, Output, Input, SimpleChanges, EventEmitter} from '@angular/core';

@Component({
    selector: 'ng-laravel-pagination',
    templateUrl: './ng-laravel-pagination.component.html',
    styleUrls: ['./ng-laravel-pagination.component.css']
})
export class NgLaravelPaginationComponent {
    @Input() data: any = null;
    @Output() changePage = new EventEmitter<any>();

    constructor() {
    }

    ngOnChanges(changes: SimpleChanges): void {
        const THIS = this;
        if (changes.data !== undefined) {
            console.log(changes.data.currentValue);
        }
    }

    toArray(num): any {
        return  Array(num).fill(null).map((x , i) => i + 1);
    }

    prevPage(): void {
        if (this.data.current_page > 1) {
            const page = this.data.current_page - 1;
            this.changePagination(page);
        }
    }

    nextPage(): void {
        if (this.data.current_page > 0 && this.data.current_page < this.data.last_page) {
            const page = this.data.current_page + 1;
            this.changePagination(page);
        }
    }

    changePagination(page): void {
        this.changePage.emit({page});
    }
}
